<template>
  <div class="space-y-6">
    
    <!-- Document Import Section -->
    <div class="macos-card p-6 bg-gradient-to-r from-blue-50 to-indigo-50 border-2 border-blue-100">
      <div class="flex items-center justify-between mb-4">
        <div class="flex items-center">
          <div class="w-12 h-12 rounded-xl bg-gradient-to-br from-blue-500 to-blue-600 flex items-center justify-center mr-4">
            <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12" />
            </svg>
          </div>
          <div>
            <h3 class="text-lg font-bold text-gray-800"> </h3>
            <p class="text-sm text-gray-600"> 2.0 (.docx) </p>
          </div>
        </div>
      </div>
      
      <div class="flex flex-col md:flex-row items-start md:items-center gap-3">
        <input type="file" accept=".docx" @change="handleFileUpload" ref="fileInput" class="hidden">
        <button @click="$refs.fileInput.click()" :disabled="isImporting" class="macos-button-primary disabled:opacity-50 disabled:cursor-not-allowed">
          <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4 inline mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12" />
          </svg>
           Word 
        </button>
        
        <div v-if="selectedFileName" class="flex items-center text-sm text-gray-700 bg-white px-3 py-2 rounded-lg">
          <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4 mr-2 text-blue-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
          </svg>
          {{ selectedFileName }}
        </div>
        
        <div v-if="isImporting" class="flex items-center text-sm text-blue-600">
          <svg class="animate-spin h-4 w-4 mr-2" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
            <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
            <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
          </svg>
          ...
        </div>
      </div>
      
      <div v-if="importError" class="mt-4 text-sm text-red-700 bg-red-50 border border-red-200 p-3 rounded-lg flex items-start">
        <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 mr-2 shrink-0 mt-0.5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
        </svg>
        <div>
          <strong></strong>{{ importError }}
        </div>
      </div>
      
      <div v-if="importSuccess" class="mt-4 text-sm text-green-700 bg-green-50 border border-green-200 p-3 rounded-lg flex items-start">
        <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 mr-2 shrink-0 mt-0.5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
        </svg>
        <div>
          <strong></strong> {{ importedFieldsCount }} 
        </div>
      </div>
    </div>

    <!-- Add Project Card - Multi-Step Form -->
    <div class="macos-card p-6">
      <div class="flex items-center justify-between mb-6">
        <h3 class="text-xl font-bold text-gray-800"></h3>
        <span class="text-sm text-gray-500"> {{ currentStep }}/4</span>
      </div>

      <!-- Step Indicator -->
      <div class="flex items-center mb-8">
        <div v-for="step in 4" :key="step" class="flex items-center flex-1">
          <div class="flex items-center justify-center w-8 h-8 rounded-full text-sm font-medium transition-all"
               :class="currentStep >= step ? 'bg-[#007AFF] text-white' : 'bg-gray-200 text-gray-500'">
            {{ step }}
          </div>
          <div v-if="step < 4" class="flex-1 h-1 mx-2 rounded" 
               :class="currentStep > step ? 'bg-[#007AFF]' : 'bg-gray-200'"></div>
        </div>
      </div>

      <!-- Step 1:  -->
      <div v-show="currentStep === 1" class="space-y-4">
        <div class="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <label class="text-sm font-medium text-gray-700 mb-1 block"> *</label>
            <input v-model="newProject.name" placeholder="" class="macos-input w-full">
          </div>
          <div>
            <label class="text-sm font-medium text-gray-700 mb-1 block"> *</label>
            <input v-model="newProject.proposalUnit" placeholder="" class="macos-input w-full">
          </div>
          <div>
            <label class="text-sm font-medium text-gray-700 mb-1 block"> *</label>
            <input v-model="newProject.proposalContact" placeholder="//Email" class="macos-input w-full">
          </div>
          <div>
            <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
            <input v-model="newProject.department" placeholder="" class="macos-input w-full">
          </div>
          <div>
            <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
            <input v-model="newProject.owner" placeholder="" class="macos-input w-full">
          </div>
          <div>
            <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
            <input v-model="relatedUnitsInput" placeholder="," class="macos-input w-full">
          </div>
          <div>
            <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
            <input v-model="newProject.startTime" type="date" class="macos-input w-full">
          </div>
          <div>
            <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
            <input v-model="newProject.dueDate" type="date" class="macos-input w-full">
          </div>
          <div>
            <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
            <select v-model="newProject.priority" class="macos-input w-full">
              <option></option>
              <option></option>
              <option></option>
            </select>
          </div>
          <div>
            <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
            <select v-model="newProject.status" class="macos-input w-full">
              <option></option>
              <option></option>
              <option></option>
              <option></option>
            </select>
          </div>
        </div>
      </div>

      <!-- Step 2:  -->
      <div v-show="currentStep === 2" class="space-y-4">
        <div>
          <label class="text-sm font-medium text-gray-700 mb-1 block"> *</label>
          <textarea v-model="newProject.projectReason" rows="3" 
                    placeholder="" 
                    class="macos-textarea w-full"></textarea>
        </div>
        <div>
          <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
          <textarea v-model="newProject.currentSituation" rows="3" 
                    placeholder=" (Who) (How) (What)" 
                    class="macos-textarea w-full"></textarea>
        </div>
        <div>
          <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
          <textarea v-model="newProject.difficulties" rows="3" 
                    placeholder="" 
                    class="macos-textarea w-full"></textarea>
        </div>
        <div>
          <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
          <textarea v-model="newProject.crossUnitIssues" rows="2" 
                    placeholder="" 
                    class="macos-textarea w-full"></textarea>
        </div>
        <div>
          <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
          <textarea v-model="newProject.attemptedSolutions" rows="2" 
                    placeholder="" 
                    class="macos-textarea w-full"></textarea>
        </div>
      </div>

      <!-- Step 3:  -->
      <div v-show="currentStep === 3" class="space-y-4">
        <div>
          <label class="text-sm font-medium text-gray-700 mb-1 block"> *</label>
          <textarea v-model="newProject.projectGoals" rows="3" 
                    placeholder=" A  3  1 " 
                    class="macos-textarea w-full"></textarea>
        </div>
        <div>
          <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
          <textarea v-model="newProject.expectedFeatures" rows="3" 
                    placeholder="" 
                    class="macos-textarea w-full"></textarea>
        </div>
        <div>
          <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
          <textarea v-model="newProject.targetUsers" rows="2" 
                    placeholder="" 
                    class="macos-textarea w-full"></textarea>
        </div>
        <div>
          <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
          <textarea v-model="newProject.expectedBenefits" rows="3" 
                    placeholder=" 50% 8 /\n" 
                    class="macos-textarea w-full"></textarea>
        </div>
        <div>
          <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
          <input v-model="newProject.estimatedTimeline" placeholder=" 3 " class="macos-input w-full">
        </div>
      </div>

      <!-- Step 4:  -->
      <div v-show="currentStep === 4" class="space-y-4">
        <div>
          <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
          <textarea v-model="newProject.successMetrics" rows="3" 
                    placeholder=" 90%" 
                    class="macos-textarea w-full"></textarea>
        </div>
        <div>
          <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
          <textarea v-model="newProject.requiredResources" rows="3" 
                    placeholder="" 
                    class="macos-textarea w-full"></textarea>
        </div>
        <div>
          <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
          <textarea v-model="newProject.riskAssessment" rows="3" 
                    placeholder="" 
                    class="macos-textarea w-full"></textarea>
        </div>
        <div>
          <label class="text-sm font-medium text-gray-700 mb-1 block">%</label>
          <input v-model.number="newProject.progress" type="number" min="0" max="100" placeholder="0-100" class="macos-input w-full">
        </div>
        <div>
          <label class="text-sm font-medium text-gray-700 mb-1 block"></label>
          <textarea v-model="newProject.description" rows="2" 
                    placeholder="" 
                    class="macos-textarea w-full"></textarea>
        </div>
      </div>

      <!-- Navigation Buttons -->
      <div class="flex justify-between mt-6 pt-6 border-t border-gray-100">
        <button v-if="currentStep > 1" @click="currentStep--" class="macos-button-secondary">
          
        </button>
        <div v-else></div>
        <div class="flex gap-3">
          <button v-if="currentStep < 4" @click="currentStep++" class="macos-button-primary">
            
          </button>
          <button v-else @click="addProject" :disabled="isLoading || !canSubmit" 
                  class="macos-button-primary disabled:opacity-50 disabled:cursor-not-allowed">
            <span v-if="isLoading">...</span>
            <span v-else></span>
          </button>
        </div>
      </div>
    </div>

    <!-- Project Cards - Trello Style -->
    <div class="macos-card p-6">
      <div class="flex items-center justify-between mb-6">
        <h3 class="text-xl font-bold text-gray-800"></h3>
        <span class="text-sm text-gray-500"> {{ filteredProjects.length }} </span>
      </div>

      <!-- Search & Filter -->
      <div class="flex flex-col md:flex-row gap-3 mb-6">
        <div class="flex-1 relative">
          <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4 absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
          </svg>
          <input v-model="searchQuery" placeholder="..." class="macos-input w-full pl-10">
        </div>
        <select v-model="statusFilter" class="macos-input w-full md:w-48">
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
      </div>

      <!-- Loading State -->
      <div v-if="isLoading" class="text-center py-12">
        <div class="inline-block animate-spin rounded-full h-8 w-8 border-4 border-gray-200 border-t-[#007AFF]"></div>
        <p class="text-gray-500 mt-3">...</p>
      </div>

      <!-- Project Cards Grid -->
      <div v-else-if="filteredProjects.length > 0" class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        <div v-for="project in filteredProjects" :key="project.id" 
             class="macos-card p-5 cursor-pointer hover:shadow-macos-hover transition-all group"
             @click="openDetail(project)">
          
          <!-- Header -->
          <div class="flex items-start justify-between mb-3">
            <h4 class="font-bold text-gray-800 text-base group-hover:text-[#007AFF] transition-colors line-clamp-2 flex-1">
              {{ project.name }}
            </h4>
            <button @click.stop="deleteProject(project.id)" 
                    class="opacity-0 group-hover:opacity-100 ml-2 p-1 hover:bg-red-50 rounded transition-all">
              <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4 text-red-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
              </svg>
            </button>
          </div>

          <!-- Meta Info -->
          <div class="flex flex-wrap items-center gap-2 mb-3">
            <span :class="{
              'status-pending': project.status === '',
              'status-active': project.status === '',
              'status-completed': project.status === '',
              'status-delayed': project.status === ''
            }">
              {{ project.status || '' }}
            </span>
            <span :class="{
              'bg-red-50 text-red-600': project.priority === '',
              'bg-yellow-50 text-yellow-600': project.priority === '',
              'bg-green-50 text-green-600': project.priority === ''
            }" class="px-2 py-1 rounded-md text-xs font-medium">
              {{ project.priority || '' }}
            </span>
          </div>

          <!-- Info -->
          <div class="text-xs text-gray-600 space-y-1 mb-3">
            <div v-if="project.proposalUnit"> {{ project.proposalUnit }}</div>
            <div v-if="project.owner"> {{ project.owner }}</div>
            <div v-if="project.dueDate"> {{ project.dueDate }}</div>
          </div>

          <!-- Progress Bar -->
          <div class="mb-2">
            <div class="flex justify-between text-xs text-gray-500 mb-1">
              <span></span>
              <span class="font-bold text-[#007AFF]">{{ project.progress || 0 }}%</span>
            </div>
            <div class="w-full bg-gray-100 rounded-full h-1.5">
              <div class="bg-gradient-to-r from-blue-500 to-blue-600 h-1.5 rounded-full transition-all" 
                   :style="{width: (project.progress || 0) + '%'}"></div>
            </div>
          </div>
        </div>
      </div>

      <!-- Empty State -->
      <div v-else class="text-center py-12">
        <svg xmlns="http://www.w3.org/2000/svg" class="h-16 w-16 mx-auto text-gray-300 mb-3" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="1.5" d="M20 13V6a2 2 0 00-2-2H6a2 2 0 00-2 2v7m16 0v5a2 2 0 01-2 2H6a2 2 0 01-2-2v-5m16 0h-2.586a1 1 0 00-.707.293l-2.414 2.414a1 1 0 01-.707.293h-3.172a1 1 0 01-.707-.293l-2.414-2.414A1 1 0 006.586 13H4" />
        </svg>
        <p class="text-gray-500"></p>
      </div>
    </div>

    <!-- Project Detail Modal -->
    <ProjectDetailModal 
      :project="selectedProject" 
      :isOpen="isModalOpen" 
      @close="closeDetail" 
    />
  </div>
</template>

<script>
import { collection, addDoc, getDocs, deleteDoc, doc } from "firebase/firestore";
import { db } from '../firebase';
import ProjectDetailModal from './ProjectDetailModal.vue';
import mammoth from 'mammoth';

export default {
  name: 'AdminProjectList',
  components: {
    ProjectDetailModal
  },
  data() {
    return {
      currentStep: 1,
      projects: [],
      newProject: this.getEmptyProject(),
      relatedUnitsInput: '',
      isLoading: false,
      searchQuery: '',
      statusFilter: '',
      selectedProject: {},
      isModalOpen: false,
      // Document Import
      selectedFileName: '',
      isImporting: false,
      importError: '',
      importSuccess: false,
      importedFieldsCount: 0
    }
  },
  computed: {
    canSubmit() {
      return this.newProject.name && this.newProject.proposalUnit && this.newProject.proposalContact;
    },
    filteredProjects() {
      return this.projects.filter(p => {
        const matchesSearch = (p.name && p.name.toLowerCase().includes(this.searchQuery.toLowerCase())) || 
                              (p.owner && p.owner.toLowerCase().includes(this.searchQuery.toLowerCase())) ||
                              (p.proposalUnit && p.proposalUnit.toLowerCase().includes(this.searchQuery.toLowerCase()));
        const matchesStatus = this.statusFilter ? p.status === this.statusFilter : true;
        return matchesSearch && matchesStatus;
      });
    }
  },
  async mounted() {
    await this.fetchProjects();
  },
  methods: {
    getEmptyProject() {
      return {
        name: '', proposalUnit: '', proposalContact: '', department: '', owner: '',
        startTime: '', dueDate: '', priority: '', progress: 0, status: '',
        projectReason: '', currentSituation: '', difficulties: '', crossUnitIssues: '',
        attemptedSolutions: '', projectGoals: '', expectedFeatures: '', targetUsers: '',
        expectedBenefits: '', estimatedTimeline: '', successMetrics: '', 
        requiredResources: '', riskAssessment: '', description: '',
        relatedUnits: []
      };
    },
    async fetchProjects() {
      this.isLoading = true;
      try {
        const querySnapshot = await getDocs(collection(db, "projects"));
        this.projects = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
      } finally {
        this.isLoading = false;
      }
    },
    async addProject() {
      if (!this.canSubmit) return alert('');
      
      this.isLoading = true;
      try {
        // Process related units
        if (this.relatedUnitsInput.trim()) {
          this.newProject.relatedUnits = this.relatedUnitsInput.split(',').map(u => u.trim()).filter(u => u);
        }

        await addDoc(collection(db, "projects"), this.newProject);
        
        // Reset form
        this.newProject = this.getEmptyProject();
        this.relatedUnitsInput = '';
        this.currentStep = 1;
        
        await this.fetchProjects();
        alert('');
      } catch (error) {
        console.error(error);
        alert(': ' + error.message);
      } finally {
        this.isLoading = false;
      }
    },
    async deleteProject(id) {
      if (!confirm('')) return;
      
      this.isLoading = true;
      try {
        await deleteDoc(doc(db, "projects", id));
        await this.fetchProjects();
      } catch (error) {
        console.error(error);
        alert('');
      } finally {
        this.isLoading = false;
      }
    },
    openDetail(project) {
      this.selectedProject = project;
      this.isModalOpen = true;
    },
    closeDetail() {
      this.isModalOpen = false;
    },
    async handleFileUpload(event) {
      const file = event.target.files[0];
      if (!file) return;

      // Reset states
      this.importError = '';
      this.importSuccess = false;
      this.importedFieldsCount = 0;

      // Validate file type
      if (!file.name.endsWith('.docx')) {
        this.importError = ' .docx  Word ';
        return;
      }

      // Validate file size (max 10MB)
      if (file.size > 10 * 1024 * 1024) {
        this.importError = ' 10MB';
        return;
      }

      this.selectedFileName = file.name;
      this.isImporting = true;

      try {
        const parsedData = await this.parseDocument(file);
        
        // Merge parsed data with current form
        this.newProject = { ...this.newProject, ...parsedData.fields };
        this.relatedUnitsInput = parsedData.relatedUnitsText || '';
        
        this.importedFieldsCount = parsedData.count;
        this.importSuccess = true;
        
        // Reset to step 1 to show imported data
        this.currentStep = 1;
      } catch (error) {
        console.error('Document parsing error:', error);
        this.importError = error.message || '';
      } finally {
        this.isImporting = false;
        // Clear file input
        event.target.value = '';
      }
    },
    async parseDocument(file) {
      try {
        const arrayBuffer = await file.arrayBuffer();
        
        // Convert to HTML to parse table structure
        const result = await mammoth.convertToHtml({ arrayBuffer });
        const html = result.value;

        if (!html || html.trim().length === 0) {
          throw new Error('');
        }

        // Create a temporary DOM element to parse HTML
        const parser = new DOMParser();
        const doc = parser.parseFromString(html, 'text/html');
        
        // Extract table rows
        const rows = doc.querySelectorAll('tr');
        
        if (rows.length === 0) {
          throw new Error('');
        }

        // Build a map of field name -> content
        const tableData = {};
        
        rows.forEach(row => {
          const cells = row.querySelectorAll('td, th');
          if (cells.length >= 2) {
            const fieldName = cells[0].textContent.trim();
            const fieldValue = cells[1].textContent.trim();
            
            // Clean up field value
            const cleanValue = fieldValue
              .replace(/\(\)/g, '')
              .replace(/^\?+$/, '')
              .replace(/^[:\s]+/, '')
              .trim();
            
            if (cleanValue && cleanValue.length > 0 && cleanValue !== '?') {
              tableData[fieldName] = cleanValue;
            }
          }
        });

        // Parse the document based on fixed format
        const fields = {};
        let count = 0;

        // Field mapping with multiple possible keywords
        const fieldMap = {
          name: [''],
          proposalUnit: ['', ''],
          proposalContact: ['', '', '/', ' / '],
          department: ['', ''],
          owner: ['', ''],
          projectReason: ['', '', ''],
          currentSituation: ['', '', '(1) '],
          difficulties: ['', '', '(2) '],
          crossUnitIssues: ['', '', '(3) ()', '()'],
          attemptedSolutions: ['', '', '', ''],
          projectGoals: ['', ''],
          expectedFeatures: ['', '', '', ''],
          targetUsers: ['', '', '', ''],
          expectedBenefits: ['', '', ''],
          estimatedTimeline: ['', ''],
          successMetrics: ['', ''],
          requiredResources: ['', ''],
          riskAssessment: ['', '', '']
        };

        // Extract related units separately
        let relatedUnitsText = '';
        const relatedUnitsKeys = ['', '/', ' / '];
        for (const key of relatedUnitsKeys) {
          if (tableData[key]) {
            relatedUnitsText = tableData[key];
            break;
          }
        }

        // Match table data to fields
        for (const [fieldKey, keywords] of Object.entries(fieldMap)) {
          for (const keyword of keywords) {
            // Try exact match first
            if (tableData[keyword]) {
              fields[fieldKey] = tableData[keyword];
              count++;
              break;
            }
            
            // Try fuzzy match (contains keyword)
            for (const [tableName, tableValue] of Object.entries(tableData)) {
              if (tableName.includes(keyword) || keyword.includes(tableName)) {
                fields[fieldKey] = tableValue;
                count++;
                break;
              }
            }
            
            if (fields[fieldKey]) break;
          }
        }

        // Special handling for multi-part fields
        // Combine (1) , (2) , (3)  into respective fields
        const combinedSituationKeys = Object.keys(tableData).filter(k => k.includes('') || k.includes('Who'));
        if (combinedSituationKeys.length > 0 && !fields.currentSituation) {
          fields.currentSituation = tableData[combinedSituationKeys[0]];
          if (!count) count++;
        }

        return {
          fields,
          relatedUnitsText,
          count
        };
      } catch (error) {
        console.error('Parsing error:', error);
        throw new Error('' + error.message);
      }
    }
  }
}
</script>
