<template>
  <div class="bg-white rounded-2xl shadow-macos p-6 mb-8">
    <h3 class="text-2xl font-bold mb-6 text-gray-800 flex items-center">
      <span class="bg-gradient-to-br from-blue-500 to-blue-600 text-white p-2.5 rounded-xl mr-3">
        <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2m-6 9l2 2 4-4" />
        </svg>
      </span>
      
    </h3>
    
    <!-- Form Section -->
    <div class="macos-card p-6 mb-6">
      <h4 class="text-lg font-bold text-gray-700 mb-4">
        {{ editingId ? '' : '' }}
      </h4>
      
      <div class="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"> *</label>
          <input v-model="newItem.name" placeholder="" class="macos-input w-full">
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"></label>
          <select v-model="newItem.category" class="macos-input w-full">
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"> *</label>
          <input v-model="newItem.assignee" placeholder="" class="macos-input w-full">
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"> *</label>
          <input v-model="newItem.frequency" placeholder="" class="macos-input w-full">
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"></label>
          <select v-model="newItem.priority" class="macos-input w-full">
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"></label>
          <select v-model="newItem.status" class="macos-input w-full">
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"></label>
          <input v-model="newItem.estimatedTime" placeholder="30" class="macos-input w-full">
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"></label>
          <input v-model="newItem.nextDueDate" type="datetime-local" class="macos-input w-full">
        </div>
      </div>

      <div class="mb-4">
        <label class="block text-sm font-medium text-gray-700 mb-1"></label>
        <textarea v-model="newItem.description" placeholder="..." rows="2" class="macos-input w-full"></textarea>
      </div>

      <div class="mb-4">
        <label class="block text-sm font-medium text-gray-700 mb-1">SOP </label>
        <input v-model="newItem.sopLink" placeholder="https://..." class="macos-input w-full">
      </div>

      <div class="flex gap-2">
        <button @click="submitForm" :disabled="isLoading" class="macos-button-primary">
          {{ isLoading ? '...' : (editingId ? '' : '') }}
        </button>
        <button v-if="editingId" @click="cancelEdit" class="macos-button-secondary">
          
        </button>
      </div>
    </div>

    <!-- Search and Filter -->
    <div class="flex flex-col md:flex-row gap-4 mb-6">
      <div class="relative flex-1">
        <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4 absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
        </svg>
        <input v-model="searchQuery" type="text" placeholder="..." class="pl-10 macos-input w-full">
      </div>
      <select v-model="statusFilter" class="macos-input">
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
      </select>
      <select v-model="categoryFilter" class="macos-input">
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
        <option value=""></option>
      </select>
    </div>

    <!-- Task Cards Grid -->
    <div class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
      <div v-for="item in filteredItems" :key="item.id" class="macos-card p-4 hover:shadow-macos-hover transition-all">
        <div class="flex items-start justify-between mb-3">
          <div class="flex-1">
            <h4 class="font-bold text-gray-800 mb-1">{{ item.name || item.title }}</h4>
            <p class="text-xs text-gray-500">{{ item.category }}</p>
          </div>
          <div class="flex gap-1">
            <span v-if="item.priority" :class="{
              'bg-red-50 text-red-600': item.priority === '',
              'bg-yellow-50 text-yellow-600': item.priority === '',
              'bg-green-50 text-green-600': item.priority === ''
            }" class="px-2 py-0.5 rounded text-xs font-bold">
              {{ item.priority }}
            </span>
            <span :class="{
              'status-active': item.status === '',
              'status-delayed': item.status === '',
              'status-pending': item.status === ''
            }">
              {{ item.status }}
            </span>
          </div>
        </div>

        <div class="space-y-2 text-sm text-gray-600 mb-4">
          <div class="flex items-center gap-2">
            <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
            </svg>
            {{ item.frequency }}
          </div>
          <div class="flex items-center gap-2">
            <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
            </svg>
            {{ item.assignee }}
          </div>
          <div v-if="item.nextDueDate" class="flex items-center gap-2 text-blue-600 font-medium">
            <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z" />
            </svg>
            {{ formatDate(item.nextDueDate) }}
          </div>
        </div>

        <div class="flex gap-2 pt-3 border-t">
          <button @click="startEdit(item)" class="flex-1 py-2 bg-blue-50 text-blue-600 rounded-lg hover:bg-blue-100 text-sm font-medium">
             
          </button>
          <button @click="deleteItem(item.id)" class="px-4 py-2 bg-red-50 text-red-600 rounded-lg hover:bg-red-100 text-sm font-medium">
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from '../firebase';
import { collection, getDocs, addDoc, deleteDoc, doc, updateDoc } from 'firebase/firestore';

export default {
  name: 'AdminRoutineList',
  data() {
    return {
      items: [],
      isLoading: false,
      editingId: null,
      newItem: this.getEmptyItem(),
      searchQuery: '',
      statusFilter: '',
      categoryFilter: ''
    };
  },
  computed: {
    filteredItems() {
      return this.items.filter(item => {
        const matchesSearch = (item.name || item.title || '').toLowerCase().includes(this.searchQuery.toLowerCase()) || 
                              ((item.assignee || '').toLowerCase().includes(this.searchQuery.toLowerCase()));
        const matchesStatus = this.statusFilter ? item.status === this.statusFilter : true;
        const matchesCategory = this.categoryFilter ? item.category === this.categoryFilter : true;
        return matchesSearch && matchesStatus && matchesCategory;
      });
    }
  },
  async mounted() {
    await this.fetchItems();
  },
  methods: {
    getEmptyItem() {
      return {
        name: '',
        category: '',
        priority: '',
        description: '',
        frequency: '',
        estimatedTime: '',
        assignee: '',
        status: '',
        isActive: true,
        completionRate: 100,
        consecutiveSuccess: 0,
        sopLink: '',
        nextDueDate: ''
      };
    },
    async fetchItems() {
      try {
        const querySnapshot = await getDocs(collection(db, 'routines'));
        this.items = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
      } catch (error) {
        console.error('Failed to fetch routines:', error);
        alert('' + error.message);
      }
    },
    async submitForm() {
      if (!this.newItem.name || !this.newItem.assignee || !this.newItem.frequency) {
        alert('');
        return;
      }

      this.isLoading = true;
      try {
        const data = {
          ...this.newItem,
          updatedAt: new Date().toISOString()
        };

        if (this.editingId) {
          await updateDoc(doc(db, 'routines', this.editingId), data);
        } else {
          data.createdAt = new Date().toISOString();
          await addDoc(collection(db, 'routines'), data);
        }

        await this.fetchItems();
        this.cancelEdit();
        alert(this.editingId ? '' : '');
      } catch (error) {
        console.error('Failed to save routine:', error);
        alert('' + error.message);
      } finally {
        this.isLoading = false;
      }
    },
    async deleteItem(id) {
      if (!confirm('')) return;
      
      try {
        await deleteDoc(doc(db, 'routines', id));
        await this.fetchItems();
        alert('');
      } catch (error) {
        console.error('Failed to delete routine:', error);
        alert('' + error.message);
      }
    },
    startEdit(item) {
      this.editingId = item.id;
      this.newItem = { ...item };
      window.scrollTo({ top: 0, behavior: 'smooth' });
    },
    cancelEdit() {
      this.newItem = this.getEmptyItem();
      this.editingId = null;
    },
    formatDate(dateStr) {
      if (!dateStr) return '';
      const date = new Date(dateStr);
      return date.toLocaleDateString('zh-TW');
    }
  }
};
</script>
