<template>
  <div class="bg-white rounded-2xl shadow-macos p-6 mb-8">
    <h3 class="text-2xl font-bold mb-6 text-gray-800 flex items-center">
      <span class="bg-gradient-to-br from-red-500 to-red-600 text-white p-2.5 rounded-xl mr-3">
        <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M11 5.882V19.24a1.76 1.76 0 01-3.417.592l-2.147-6.15M18 13a3 3 0 100-6M5.436 13.683A4.001 4.001 0 017 6h1.832c4.1 0 7.625-1.234 9.168-3v14c-1.543-1.766-5.067-3-9.168-3H7a3.988 3.988 0 01-1.564-.317z" />
        </svg>
      </span>
      
    </h3>
    
    <!-- Form Section -->
    <div class="macos-card p-6 mb-6">
      <h4 class="text-lg font-bold text-gray-700 mb-4">
        {{ editingId ? '' : '' }}
      </h4>
      
      <div class="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"> *</label>
          <input v-model="newItem.name" placeholder="" class="macos-input w-full">
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"></label>
          <select v-model="newItem.type" class="macos-input w-full">
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"> *</label>
          <input v-model="newItem.date" type="date" class="macos-input w-full">
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"></label>
          <input v-model="newItem.location" placeholder="" class="macos-input w-full">
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"></label>
          <input v-model="newItem.organizer" placeholder="" class="macos-input w-full">
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"></label>
          <select v-model="newItem.status" class="macos-input w-full">
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"></label>
          <input v-model.number="newItem.currentParticipants" type="number" placeholder="0" class="macos-input w-full">
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"></label>
          <input v-model.number="newItem.maxParticipants" type="number" placeholder="200" class="macos-input w-full">
        </div>
      </div>

      <div class="mb-4">
        <label class="block text-sm font-medium text-gray-700 mb-1"></label>
        <textarea v-model="newItem.description" placeholder="..." rows="2" class="macos-input w-full"></textarea>
      </div>

      <div class="flex gap-2">
        <button @click="submitForm" :disabled="isLoading" class="macos-button-primary">
          {{ isLoading ? '...' : (editingId ? '' : '') }}
        </button>
        <button v-if="editingId" @click="cancelEdit" class="macos-button-secondary">
          
        </button>
      </div>
    </div>

    <!-- Search -->
    <div class="mb-6">
      <div class="relative">
        <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4 absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
        </svg>
        <input v-model="searchQuery" type="text" placeholder="..." class="pl-10 macos-input w-full">
      </div>
    </div>

    <!-- Event Cards Grid -->
    <div class="grid grid-cols-1 md:grid-cols-2 gap-4">
      <div v-for="item in filteredItems" :key="item.id" class="macos-card overflow-hidden hover:shadow-macos-hover transition-all">
        <div class="flex">
          <div class="bg-red-100 text-red-600 p-4 flex flex-col items-center justify-center min-w-[90px]">
            <span class="text-xs font-bold">Date</span>
            <span class="text-lg font-bold mt-1">{{ formatDate(item.date) }}</span>
          </div>
          <div class="p-4 flex-grow">
            <div class="flex items-start justify-between mb-2">
              <h4 class="font-bold text-gray-800 flex-1">{{ item.name }}</h4>
              <span v-if="item.status" :class="{
                'status-active': item.status === '',
                'status-delayed': item.status === '',
                'bg-gray-100 text-gray-600': item.status === ''
              }" class="ml-2">
                {{ item.status }}
              </span>
            </div>

            <div class="space-y-1 text-sm text-gray-600">
              <div v-if="item.location" class="flex items-center gap-2">
                <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
                </svg>
                {{ item.location }}
              </div>
              <div v-if="item.organizer" class="flex items-center gap-2">
                <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 21V5a2 2 0 00-2-2H7a2 2 0 00-2 2v16m14 0h2m-2 0h-5m-9 0H3m2 0h5M9 7h1m-1 4h1m4-4h1m-1 4h1m-5 10v-5a1 1 0 011-1h2a1 1 0 011 1v5m-4 0h4" />
                </svg>
                {{ item.organizer }}
              </div>
              <div v-if="item.currentParticipants" class="flex items-center gap-2 text-red-600 font-medium">
                 {{ item.currentParticipants }}{{ item.maxParticipants ? '/' + item.maxParticipants : '' }} 
              </div>
            </div>
          </div>
        </div>

        <div class="flex gap-2 px-4 pb-4">
          <button @click="startEdit(item)" class="flex-1 py-2 bg-red-50 text-red-600 rounded-lg hover:bg-red-100 text-sm font-medium">
             
          </button>
          <button @click="deleteItem(item.id)" class="px-4 py-2 bg-gray-50 text-gray-600 rounded-lg hover:bg-gray-100 text-sm font-medium">
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from '../firebase';
import { collection, getDocs, addDoc, deleteDoc, doc, updateDoc } from 'firebase/firestore';

export default {
  name: 'AdminEventList',
  data() {
    return {
      items: [],
      isLoading: false,
      editingId: null,
      newItem: this.getEmptyItem(),
      searchQuery: ''
    };
  },
  computed: {
    filteredItems() {
      return this.items.filter(item => {
        const matchesSearch = (item.name || '').toLowerCase().includes(this.searchQuery.toLowerCase()) || 
                              ((item.organizer || '').toLowerCase().includes(this.searchQuery.toLowerCase()));
        return matchesSearch;
      });
    }
  },
  async mounted() {
    await this.fetchItems();
  },
  methods: {
    getEmptyItem() {
      return {
        name: '',
        type: '',
        date: '',
        location: '',
        organizer: '',
        status: '',
        currentParticipants: 0,
        maxParticipants: 200,
        description: ''
      };
    },
    async fetchItems() {
      try {
        const querySnapshot = await getDocs(collection(db, 'events'));
        this.items = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
      } catch (error) {
        console.error('Failed to fetch events:', error);
        alert('' + error.message);
      }
    },
    async submitForm() {
      if (!this.newItem.name || !this.newItem.date) {
        alert('');
        return;
      }

      this.isLoading = true;
      try {
        const data = {
          ...this.newItem,
          updatedAt: new Date().toISOString()
        };

        if (this.editingId) {
          await updateDoc(doc(db, 'events', this.editingId), data);
        } else {
          data.createdAt = new Date().toISOString();
          await addDoc(collection(db, 'events'), data);
        }

        await this.fetchItems();
        this.cancelEdit();
        alert(this.editingId ? '' : '');
      } catch (error) {
        console.error('Failed to save event:', error);
        alert('' + error.message);
      } finally {
        this.isLoading = false;
      }
    },
    async deleteItem(id) {
      if (!confirm('')) return;
      
      try {
        await deleteDoc(doc(db, 'events', id));
        await this.fetchItems();
        alert('');
      } catch (error) {
        console.error('Failed to delete event:', error);
        alert('' + error.message);
      }
    },
    startEdit(item) {
      this.editingId = item.id;
      this.newItem = { ...item };
      window.scrollTo({ top: 0, behavior: 'smooth' });
    },
    cancelEdit() {
      this.newItem = this.getEmptyItem();
      this.editingId = null;
    },
    formatDate(dateStr) {
      if (!dateStr) return '';
      const date = new Date(dateStr);
      return date.toLocaleDateString('zh-TW', { month: '2-digit', day: '2-digit' });
    }
  }
};
</script>
