<template>
  <div class="bg-white rounded-2xl shadow-macos p-6 mb-8">
    <h3 class="text-2xl font-bold mb-6 text-gray-800 flex items-center">
      <span class="bg-gradient-to-br from-green-500 to-green-600 text-white p-2.5 rounded-xl mr-3">
        <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M17 8h2a2 2 0 012 2v6a2 2 0 01-2 2h-2v4l-4-4H9a1.994 1.994 0 01-1.414-.586m0 0L11 14h4a2 2 0 002-2V6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2v4l.586-.586z" />
        </svg>
      </span>
      /
    </h3>
    
    <!-- Form Section -->
    <div class="macos-card p-6 mb-6">
      <h4 class="text-lg font-bold text-gray-700 mb-4">
        {{ editingId ? '' : '' }}
      </h4>
      
      <div class="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"> *</label>
          <input v-model="newItem.topic" placeholder="" class="macos-input w-full">
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"></label>
          <select v-model="newItem.type" class="macos-input w-full">
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"> *</label>
          <input v-model="newItem.date" type="date" class="macos-input w-full">
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"></label>
          <input v-model="newItem.speaker" placeholder="" class="macos-input w-full">
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"></label>
          <input v-model="newItem.role" placeholder="" class="macos-input w-full">
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"></label>
          <select v-model="newItem.status" class="macos-input w-full">
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"></label>
          <input v-model.number="newItem.currentParticipants" type="number" placeholder="0" class="macos-input w-full">
        </div>
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1"></label>
          <input v-model.number="newItem.maxParticipants" type="number" placeholder="30" class="macos-input w-full">
        </div>
      </div>

      <div class="mb-4">
        <label class="block text-sm font-medium text-gray-700 mb-1"></label>
        <textarea v-model="newItem.description" placeholder="..." rows="2" class="macos-input w-full"></textarea>
      </div>

      <div class="mb-4">
        <label class="block text-sm font-medium text-gray-700 mb-1"></label>
        <input v-model="newItem.link" placeholder="https://..." class="macos-input w-full">
      </div>

      <div class="flex gap-2">
        <button @click="submitForm" :disabled="isLoading" class="macos-button-primary">
          {{ isLoading ? '...' : (editingId ? '' : '') }}
        </button>
        <button v-if="editingId" @click="cancelEdit" class="macos-button-secondary">
          
        </button>
      </div>
    </div>

    <!-- Search -->
    <div class="mb-6">
      <div class="relative">
        <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4 absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
        </svg>
        <input v-model="searchQuery" type="text" placeholder="..." class="pl-10 macos-input w-full">
      </div>
    </div>

    <!-- Forum Cards Grid -->
    <div class="grid grid-cols-1 md:grid-cols-2 gap-4">
      <div v-for="item in filteredItems" :key="item.id" class="macos-card p-4 hover:shadow-macos-hover transition-all">
        <div class="flex items-start justify-between mb-3">
          <div class="flex-1">
            <div class="text-xs text-green-600 font-bold mb-1">{{ item.type }}</div>
            <h4 class="font-bold text-gray-800">{{ item.name || item.topic }}</h4>
          </div>
          <span v-if="item.status" :class="{
            'status-active': item.status === '',
            'status-delayed': item.status === '',
            'bg-gray-100 text-gray-600': item.status === ''
          }" class="ml-2">
            {{ item.status }}
          </span>
        </div>

        <div class="space-y-2 text-sm text-gray-600 mb-4">
          <div class="flex items-center gap-2">
            <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z" />
            </svg>
            {{ item.date }}
          </div>
          <div v-if="item.speaker" class="flex items-center gap-2">
            <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
            </svg>
            {{ item.speaker }}
          </div>
          <div v-if="item.currentParticipants" class="flex items-center gap-2 text-green-600 font-medium">
            <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M17 20h5v-2a3 3 0 00-5.356-1.857M17 20H7m10 0v-2c0-.656-.126-1.283-.356-1.857M7 20H2v-2a3 3 0 015.356-1.857M7 20v-2c0-.656.126-1.283.356-1.857m0 0a5.002 5.002 0 019.288 0M15 7a3 3 0 11-6 0 3 3 0 016 0zm6 3a2 2 0 11-4 0 2 2 0 014 0zM7 10a2 2 0 11-4 0 2 2 0 014 0z" />
            </svg>
            {{ item.currentParticipants }}{{ item.maxParticipants ? '/' + item.maxParticipants : '' }} 
          </div>
        </div>

        <div class="flex gap-2 pt-3 border-t">
          <button @click="startEdit(item)" class="flex-1 py-2 bg-green-50 text-green-600 rounded-lg hover:bg-green-100 text-sm font-medium">
             
          </button>
          <button @click="deleteItem(item.id)" class="px-4 py-2 bg-red-50 text-red-600 rounded-lg hover:bg-red-100 text-sm font-medium">
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from '../firebase';
import { collection, getDocs, addDoc, deleteDoc, doc, updateDoc } from 'firebase/firestore';

export default {
  name: 'AdminForumList',
  data() {
    return {
      items: [],
      isLoading: false,
      editingId: null,
      newItem: this.getEmptyItem(),
      searchQuery: ''
    };
  },
  computed: {
    filteredItems() {
      return this.items.filter(item => {
        const matchesSearch = (item.name || item.topic || '').toLowerCase().includes(this.searchQuery.toLowerCase()) || 
                              ((item.speaker || '').toLowerCase().includes(this.searchQuery.toLowerCase()));
        return matchesSearch;
      });
    }
  },
  async mounted() {
    await this.fetchItems();
  },
  methods: {
    getEmptyItem() {
      return {
        topic: '',
        type: '',
        date: '',
        speaker: '',
        role: '',
        status: '',
        currentParticipants: 0,
        maxParticipants: 30,
        description: '',
        link: ''
      };
    },
    async fetchItems() {
      try {
        const querySnapshot = await getDocs(collection(db, 'forums'));
        this.items = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
      } catch (error) {
        console.error('Failed to fetch forums:', error);
        alert('' + error.message);
      }
    },
    async submitForm() {
      if (!this.newItem.topic || !this.newItem.date) {
        alert('');
        return;
      }

      this.isLoading = true;
      try {
        const data = {
          ...this.newItem,
          name: this.newItem.topic, // 
          updatedAt: new Date().toISOString()
        };

        if (this.editingId) {
          await updateDoc(doc(db, 'forums', this.editingId), data);
        } else {
          data.createdAt = new Date().toISOString();
          await addDoc(collection(db, 'forums'), data);
        }

        await this.fetchItems();
        this.cancelEdit();
        alert(this.editingId ? '' : '');
      } catch (error) {
        console.error('Failed to save forum:', error);
        alert('' + error.message);
      } finally {
        this.isLoading = false;
      }
    },
    async deleteItem(id) {
      if (!confirm('')) return;
      
      try {
        await deleteDoc(doc(db, 'forums', id));
        await this.fetchItems();
        alert('');
      } catch (error) {
        console.error('Failed to delete forum:', error);
        alert('' + error.message);
      }
    },
    startEdit(item) {
      this.editingId = item.id;
      this.newItem = { ...item, topic: item.topic || item.name };
      window.scrollTo({ top: 0, behavior: 'smooth' });
    },
    cancelEdit() {
      this.newItem = this.getEmptyItem();
      this.editingId = null;
    }
  }
};
</script>
